body {
    font-family: "Montserrat", sans-serif;
    margin: 0 auto;
    padding: 25px;
    max-width: 400px;
    min-width: 400px;
    background-color: #dfe6e9;
    text-align: center;
}

.heading {
    text-align: center;
    font-size: 2em;
    color: #2d3436;
    margin: 20px 0 30px;
}

.card {
    border-radius: 25px;
    position: relative;
    padding: 25px 15px;
    background-color: #81ecec;
    margin: 50px 0;
    height: 200px;
    box-shadow: 0 2px 5px #ccc;
    text-align: left;
}

.top {
    border-radius: 25px 25px 0 0;
    height: 100px;
    width: 100%;
    background-color: #00cec9;
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
}

.name {
    font-size: 2em;
    color: #2d3436;
    display: flex;
    flex: 1;
    margin: 10% 20px 0;
}

.card img {
    margin: 30px 20px 0 0;
}

.circle-img {
    border-radius: 50%;
    border: 7px solid #fff;
    width: 120px;
    height: 120px;
}

.bottom {
    margin-top: 120px;
}

.info {
    margin: 20px 0;
    color: #1a7e76;
}

.dictionary {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: space-between;
    margin: 4rem auto;
    max-width: 90%;
}

.dictionary > .term {
    text-align: center;
    max-width: 20rem;
    margin-bottom: 3rem;
    padding: 2rem;
    border-radius: 3px;
    box-shadow: 0 2px 0 rgba(0, 0, 0, 0.05), 0 5px 20px rgba(0, 0, 0, 0.1);
    background-color: #fff;
    transition: all 100ms ease-in-out;
}

.dictionary > .term > dt {
    margin-bottom: 1rem;
}

dt {
    color: #2ec2b0;
    line-height: 1;
}

dt span {
    font-size: 200%;
    font-weight: 900;
}

.emoji {
    margin: 0 auto 20px;
    display: block;
    font-size: 400%;
}

dd {
    font-size: 92.5%;
    margin-left: 0;
}

dd dt {
    margin-top: 1rem;
    margin-bottom: 0.25rem;
    font-weight: 600;
    font-size: inherit;
}

dd dl {
    font-size: 90%;
    margin: 1rem auto 0 auto;
    letter-spacing: 0.33px;
    max-width: 15rem;
}

dd dl .term {
    margin-bottom: 1rem;
}

input{
    display: block!important;
}

code{
    font-family: 'Source Code Pro', monospace;
    font-size: 0.5rem;
}

hr{
    border-style: solid;
    background: black;
    border-width: 0.5px;
}

button{
    padding: 0.5rem 3rem;
    background-color: #2ec2b0;
    color: white;
    font-weight: bold;
    border: 0;
}